import { Axios } from 'axios'
import {
  VaultCategory,
  VaultCategoryData,
  VaultCategoryRoutes,
  VaultCategoriesClientModel,
  VaultCategoriesSearchQuery,
} from '../vault-category.model'
import { ApiEntity } from '../../core'

export class VaultCategoriesClient
  extends ApiEntity
  implements VaultCategoriesClientModel
{
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Create a vault category
   * @param body - the category data
   * @returns the created category
   */
  public create(body: VaultCategoryData) {
    return this._create<VaultCategory>(VaultCategoryRoutes.v1Create, body)
  }

  /**
   * Search categories by params
   * @param searchParams - the filters to apply
   * @returns a categories page
   */
  public search(searchParams?: VaultCategoriesSearchQuery) {
    return this._search<VaultCategory>(
      VaultCategoryRoutes.v1Search,
      searchParams
    )
  }

  /**
   * List categories by params
   * @param searchParams - the filters to apply
   * @returns the categories list
   */
  public list(searchParams?: VaultCategoriesSearchQuery) {
    return this._list<VaultCategory>(VaultCategoryRoutes.v1Search, searchParams)
  }

  /**
   * Find a category by params
   * @param searchParams - the filters to apply
   * @returns an optional category
   */
  public findOne(searchParams?: VaultCategoriesSearchQuery) {
    return this._findOne<VaultCategory>(
      VaultCategoryRoutes.v1Search,
      searchParams
    )
  }
}
